.login-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
    width:100%;
}

.login {
    padding:2.5rem;
    justify-content: center;
    align-items: center;
    max-width: 18%;
    word-wrap: break-word;
}
.change-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
    width:100%;
}

.change {
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap:1.5rem;
    gap:1.5rem;
    padding:3.5rem;
}

.create-schema-wrapper {
    padding:1rem;
    flex: 1 1;
}

.create-schema {
    display:grid;
    grid-template-columns: 49% 49%;
    grid-gap:2%;
    gap:2%;
    padding:1rem;
    min-height: 100%;
}

.creation-tut {
    display:flex;
    flex-direction: column;
    grid-gap:1rem;
    gap:1rem;
    align-items: left;
}
.db-information {
    min-height: 100%;
    min-width: 100%;
    display:flex;
    flex-direction: column;
}

.view-controls {
    background-color: white;
    padding:0rem 0.5rem;
}

.section {
    padding:1rem;
    overflow-x: hidden;
    overflow-y: scroll;
    max-height: 49.25rem;
}

.section-col {
    display:flex;
    flex-direction: column;
    grid-gap:1rem;
    gap:1rem;
}

.create-wrapper {
    display:flex;
    flex-direction: column;
    height:100%;
    width:100%;
}

.create {
    height:100%;
}

.editor {
    flex:1 1;
}
.index-wrapper {
    display:flex;
    flex-direction: column;
    height:100%;
    grid-gap:1rem;
    gap:1rem;
}

.editor {
    height:90%;
    flex:1 1;
}
.index-controls {
    padding:1rem;
    justify-content: space-between;
}
.field {
    display:flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    padding:5px;
    grid-gap:0.5rem;
    gap:0.5rem;
    margin:0rem 1rem;
}

.full-document {
    display:grid;
    grid-template-columns: 50% 50%;
    min-height:150px;
}

.inner-document-controls {
    display:flex;
    flex-direction: column;
    max-width: 50%;
    grid-gap:1rem;
    gap:1rem;
    padding:1rem;
}

.document-operations {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding:1rem;
    grid-gap:1rem;
    gap:1rem;
    height:100%;
}
.all-schemas {
    display:flex;
    flex-direction: column;
    flex:1 1;
}

.schema-controls {
    background:#fff;
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding:0.15rem 1rem;
}

.schema-details {
    flex:1 1;
    display:grid;
    grid-template-columns: 20% 80%;
    height:100%;
}

.schema-interaction-controls {
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #fff;
}

.connected-to {
    padding:1rem;
}

.schema-view-details {
    height:100%;
}

.small-width {
    width:50%;
}

.part {
    padding:1rem;
}
.create-database {
    padding:2.5rem;
    align-items: center;
    justify-content: center;
    grid-gap:1rem;
    gap:1rem;
}
.users-controls {
    display:grid;
    grid-template-columns: 15% 85%;
    height:100%;
}

.body {
    padding:1rem;
}

.overflow-scroll {
    overflow-y: scroll;
}

.create-user {
    background-color: #fff;
    padding: 1rem;   
}

.main-form {
    width:25%;
}
.dashboard {
    width:100%;
    height:100%;
    display:grid;
    grid-template-rows: -webkit-max-content auto;
    grid-template-rows: max-content auto;
}

.logout {
    display:flex;flex-direction: row;
    justify-content: flex-end;align-items: center;
}

.links {
    flex:1 1;
    display:flex;
    justify-content: center;
    align-items: center;
}

.header {
    display:flex;
    flex-direction: column;
}

.sub-header-1 {
    display:grid;
    grid-template-columns: 15% 70% 15%;
    padding:0.5rem 1rem;
}

.sub-header-2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding:0.15rem 1rem;
}

.create-database-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
}

.main-body {
    background: #f0f0f0;
}
.cluster {
    padding:1rem;
    height:100%;
    width:100%;
}

.node {
    padding:1.5rem;
    height:20rem;
}

.grid {
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 40rem;
    display:grid;
    grid-template-columns: repeat(4, 24.3%);
    padding:1rem;
    grid-gap:1rem;
    gap:1rem;
}
*, *::after, *::before {
    box-sizing: border-box;
    padding:0;
    margin:0;
}

#root, body, html {
    height: 100vh;
    width: 100vw;
    overflow:hidden;
}

.flex-row-gap {
    display:flex;
    flex-direction: row;
    grid-gap:1rem;
    gap:1rem;
}

.flex-col-gap {
    display:flex;
    flex-direction: column;
    grid-gap:1rem;
    gap:1rem;
}

.flex-row {
    display:flex;
    flex-direction: row;
}

.flex-col {
    display:flex;
    flex-direction: column;
}

.align-center {
    align-items: center;
}

.padding {
    padding:1rem;
}
